import React, { useState, useEffect } from 'react';

import { ResponsiveSankey } from '@nivo/sankey'
import Card from 'react-bootstrap/Card';

import data from '../assets/data/sankey_country_group.json'

function LOFRaceSankey() {
    const [breeds, setBreeds] = useState([{}]);
    
    useEffect(() => {
        setBreeds(data);
      }, []);
    
    return (
        
        <Card className="mt-4">
            <Card.Body>
                <Card.Title>Relation entre le pays d'origine et le groupe FCI</Card.Title>
                <Card.Text style={{height: "600px"}}>
                <ResponsiveSankey
                    data={breeds}
                    margin={{ top: 25, right: 25, bottom: 25, left: 25 }}
                    align="justify"
                    sort="descending"
                    colors={{ scheme: 'category10' }}
                    nodeOpacity={1}
                    nodeHoverOthersOpacity={0.35}
                    nodeThickness={18}
                    nodeSpacing={24}
                    nodeBorderWidth={0}
                    nodeBorderColor={{
                        from: 'color',
                        modifiers: [
                            [
                                'darker',
                                0.8
                            ]
                        ]
                    }}
                    nodeBorderRadius={3}
                    linkOpacity={0.5}
                    linkHoverOthersOpacity={0.1}
                    linkContract={0}
                    enableLinkGradient={true}
                    labelPosition="outside"
                    labelOrientation="vertical"
                    labelPadding={16}
                    labelTextColor={{
                        from: 'color',
                        modifiers: [
                            [
                                'darker',
                                1
                            ]
                        ]
                    }}
                    
                />
                    </Card.Text>
        </Card.Body>
        </Card>
    );
}
    
export default LOFRaceSankey;