import React, { useState, useEffect } from 'react';

import { ResponsiveScatterPlot } from '@nivo/scatterplot'
import { TooltipWrapper } from '@nivo/tooltip';
import Card from 'react-bootstrap/Card';

import data from '../assets/data/scatter_weight_height.json'

function LOFRaceScatter() {
    const [breeds, setBreeds] = useState([{}]);
    
    useEffect(() => {
        setBreeds(data);
      }, []);

    const tooltip = props => {
        return (
            
        <TooltipWrapper position={[0, 0]}>
            <Card text="light" bg="secondary" className="breedTooltip">
                <Card.Body>
                    <Card.Header>{props['node']['data']['name']}</Card.Header>
                    <Card.Text>
                        <Card.Img src={'http://cedricmaigrot.fr/fci/' + props['node']['data']['idFCI'] + '.jpg' }/>
                        <ul>
                            <li><span style={{fontWeight: "bold", color : props['node']['color']}}></span> {props['node']['serieId']}</li>
                            <li>{props['node']['formattedX']} kg / {props['node']['formattedY']} cm</li>
                        </ul>
                        {/* {JSON.stringify(props, null, 2)} */}
                    </Card.Text>
                </Card.Body>
            </Card>
        </TooltipWrapper>
        );
    };
    
    return (
        <div bg="light" variant="light">
        
        <Card className="mt-4">
            <Card.Body>
                <Card.Title>Relation entre la taille et le poids des races de chiens</Card.Title>
                <Card.Text style={{height: "600px"}}>
                   <ResponsiveScatterPlot
                    data={breeds}
                    tooltip={tooltip}
                    margin={{ top: 20, right: 20, bottom: 100, left: 75 }}
                    xScale={{ type: 'linear', min: 0, max: 'auto' }}
                    xFormat=">-.2f"
                    yScale={{ type: 'linear', min: 0, max: 'auto' }}
                    yFormat=">-.2f"
                    colors={{ scheme: 'category10' }}
                    blendMode="multiply"
                    axisTop={null}
                    axisRight={null}
                    axisBottom={{
                        orient: 'bottom',
                        tickSize: 5,
                        tickPadding: 5,
                        tickRotation: 0,
                        legend: 'Poids (kg)',
                        legendPosition: 'middle',
                        legendOffset: 46
                    }}
                    axisLeft={{
                        orient: 'left',
                        tickSize: 5,
                        tickPadding: 5,
                        tickRotation: 0,
                        legend: 'Taille (cm)',
                        legendPosition: 'middle',
                        legendOffset: -60
                    }}
                    legends={[
                        {
                            anchor: 'bottom-left',
                            direction: 'row',
                            justify: false,
                            translateX: 0,
                            translateY: 100,
                            itemWidth: 100,
                            itemHeight: 50,
                            itemsSpacing: 5,
                            itemDirection: 'left-to-right',
                            symbolSize: 12,
                            symbolShape: 'circle',
                            effects: [
                                {
                                    on: 'hover',
                                    style: {
                                        itemOpacity: 1
                                    }
                                }
                            ]
                        }
                    ]}
                />
                    </Card.Text>
        </Card.Body>
        </Card>
        </div>
    );
}
    
export default LOFRaceScatter;