import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

function NavLayout() {
  return (
    <>
      <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.2.0/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-gH2yIJqKdNHPEq0n4Mqa/HGKIhSkIHeL5AyhkYV8i59U5AR6csBvApHHNl/vI1Bx" crossorigin="anonymous"></link>

      <Navbar className="px-4" fluid bg="dark" variant="dark" expand="lg">
          <Navbar.Brand href="#home">Doggo</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link href="/">Home</Nav.Link>
            <Nav.Link href="/breeds">Races</Nav.Link>
            <NavDropdown title="Analyses" id="collasible-nav-dropdown">
              <NavDropdown.Item href="/analyzes/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="/analyzes/3.2">
                Another action
              </NavDropdown.Item>
              <NavDropdown.Item href="/analyzes/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="/analyzes/3.4">
                Separated link
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <Nav>
            <Nav.Link href="http://cedricmaigrot.fr:8080/">
              Retour sur mon porfolio
            </Nav.Link>
          </Nav>
      </Navbar>
    </>
  );
}

export default NavLayout;