import React, { useState, useEffect } from 'react';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import LOFRaceScatter from './LOFRaceScatter';
import LOFRaceSankey from './LOFRaceSankey';

function LOFRace() {
    
    return (
        <Row>
            <Col md={6}>
                <LOFRaceScatter />
            </Col>
            <Col md={6}>
                <LOFRaceSankey />
            </Col>
        </Row>
    );
}
    
export default LOFRace;