import { HashRouter, Routes, Route } from "react-router-dom";

import HomeLayout from "./layouts/Home";
import LOFRace from "./components/LOFRace";

function App() {
    return (
    <>
      <HashRouter>
        <Routes>
          <Route path="/" element={<HomeLayout />}>
            <Route index element={<LOFRace />} />
            <Route path="breeds" element={<LOFRace />} />
          </Route>
        </Routes>
      </HashRouter>
      </>
    );
}

export default App